import { type MaybeRef, computed, ref, unref } from 'vue'
import { createGlobalState } from '@vueuse/core'

const useIdState = createGlobalState(() => {
  const count = ref(0)

  return { count }
})

export function useId(manualId?: MaybeRef<string | null | undefined>) {
  const { count } = useIdState()

  const id = `portal-${++count.value}`

  return computed(() => unref(manualId) ?? id)
}
